

































































































@import '@/design/index.scss';

.header {
  position: relative;
  width: 100%;
  color: #fff;
  background-color: rgba(7, 17, 27, 0.5);
  overflow: hidden;
  .content-wrapper {
    position: relative;
    display: flex;
    font-size: 0;
    padding: 24px 12px 18px 24px;
    .avatar {
      img {
        border-radius: 2px;
      }
    }
    .content {
      margin-left: 16px;
      .title {
        font-size: 0;
        margin: 2px 0 8px;
        .brand {
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          @include bg-image('header/brand');
          background-size: 30px 18px;
          background-repeat: no-repeat;
        }
        .name {
          margin-left: 6px;
          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
        }
      }
      .description {
        line-height: 12px;
        font-size: 12px;
        margin-bottom: 10px;
      }
      .supports {
        font-size: 0;
        .icon {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 4px;
          background-size: 12px;
          background-repeat: no-repeat;
          &.decrease {
            @include bg-image('header/decrease_1');
          }
          &.discount {
            @include bg-image('header/discount_1');
          }
          &.guarantee {
            @include bg-image('header/guarantee_1');
          }
          &.invoice {
            @include bg-image('header/invoice_1');
          }
          &.special {
            @include bg-image('header/special_1');
          }
        }
        .text {
          line-height: 12px;
          font-size: 10px;
        }
      }
    }
    .supports-count {
      position: absolute;
      right: 12px;
      bottom: 18px;
      padding: 0 8px;
      height: 24px;
      line-height: 24px;
      font-size: 0;
      border-radius: 14px;
      background: rgba(0, 0, 0, 0.2);
      text-align: center;
      .count {
        font-size: 10px;
        vertical-align: middle;
      }
      .icon-keyboard_arrow_right {
        display: inline-block;
        font-size: 10px;
        margin-left: 2px;
        vertical-align: middle;
      }
    }
  }
  .bulletin-wrapper {
    position: relative;
    height: 28px;
    line-height: 28px;
    background: rgba(7, 17, 27, 0.2);
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    .title {
      display: inline-block;
      width: 22px;
      height: 12px;
      @include bg-image('header/bulletin');
      background-size: 22px 12px;
      background-repeat: no-repeat;
      vertical-align: middle;
    }
    .text {
      margin: 0 4px;
      font-size: 10px;
    }
    .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      right: 12px;
      bottom: 8px;
    }
  }
  .background {
    position: absolute;
    top: -20%;
    right: 0;
    width: 100%;
    z-index: -1;
    filter: blur(10px);
  }
  .detail {
    top: 0;
    right: 0;
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    overflow: auto;
    background: rgba(7, 17, 27, 0.8);
    &.fade-enter-active, &.fade-leave-active {
      transition: all .5s ease;
    }
    &.fade-enter, &.fade-leave-to {
      transform: translateY(10px);
      opacity: 0;
    }
    .detail-wrapper {
      min-height: 100%;
      display: flex;
      flex-direction: column;
      width: 100%;
      font-size: 0;
      .detail-main {
        flex: 1;
        padding-top: 64px;
        .name {
          text-align: center;
          font-size: 16px;
          line-height: 16px;
          font-weight: 700;
        }
        .star {
          margin-top: 18px;
          padding: 2px 0;
          text-align: center;
        }
        .title {
          display: flex;
          width: 80%;
          margin: 30px auto 24px;
          .line {
            flex: 1;
            position: relative;
            @include border-1px(rgba(255, 255, 255, 0.2));
            top: -6px;
          }
          .text {
            padding: 0 12px;
            line-height: 14px;
            font-size: 14px;
            font-weight: 700;
          }
        }
        .supports {
          width: 80%;
          margin: 0 auto;
          .support-item {
            padding: 0 12px;
            margin-bottom: 12px;
            font-size: 0;
            &:last-child {
              margin-bottom: 0;
            }
            .icon {
              display: inline-block;
              width: 16px;
              height: 16px;
              vertical-align: top;
              background-size: 16px;
              background-repeat: no-repeat;
              &.decrease {
                @include bg-image('header/decrease_2');
              }
              &.discount {
                @include bg-image('header/discount_2');
              }
              &.guarantee {
                @include bg-image('header/guarantee_2');
              }
              &.invoice {
                @include bg-image('header/invoice_2');
              }
              &.special {
                @include bg-image('header/special_2');
              }
            }
            .text {
              margin-left: 6px;
              font-size: 12px;
              line-height: 16px;
            }
          }
        }
        .bulletin {
          width: 80%;
          margin: 0 auto;
          .content {
            padding: 0 12px;
            line-height: 24px;
            font-size: 12px;
          }
        }
      }
      .detail-close {
        width: 32px;
        height: 32px;
        margin: 0 auto 32px;
        clear: both;
        font-size: 32px;
      }
    }
  }
}
