














































@import './design/index.scss';

#app {
  #tab {
    @include border-1px( rgba(7, 17, 27, 0.1));
    position: sticky;
    top: 0;
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #fff;
    z-index: 20;
    a {
      flex: 1;
      text-align: center;
      display: block;
      font-size: 14px;
      color: rgb(77, 85, 93);
      &.router-link-active {
        font-size: 14px;
        color: rgb(240, 20, 20);
      }
    }
  }
}
